:root {
    --primary-color: #0f1014;
    --overlay-color: #16181d;
    --hover-color: #00a0fd;
    --text-color: #ffffffee;


    --vin-color: radial-gradient(circle at 45% 89%, rgb(27, 29, 36), rgba(15, 16, 20, 0) 50%), 
    radial-gradient(circle at 14% 10%,   rgba(39, 66, 88, 0.644), rgba(97, 106, 141, 0) 50%), 
    radial-gradient(circle at 57% 2%, #0f1014, #0f1014 50%),
      radial-gradient(circle at 85% 70%, rgba(15, 16, 20, 1), rgba(15, 16, 20, 0) 50%), 
      radial-gradient(circle at 5% 90%, #0f1014, #0f1014 50%);


    --bg-vin-color: radial-gradient(circle at 45% 89%, rgba(15, 16, 20, 1), rgba(15, 16, 20, 0) 50%), 
    radial-gradient(circle at 14% 10%,   rgba(39, 66, 88, 0.644), rgba(97, 106, 141, 0) 50%), 
    radial-gradient(circle at 57% 2%, #0f1014, #0f1014 50%),
      radial-gradient(circle at 85% 70%, rgba(15, 16, 20, 1), rgba(15, 16, 20, 0) 50%), 
      radial-gradient(circle at 5% 90%, #0f1014, #0f1014 50%);
    --menu-speed: 0.6s;
}

* {
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Hind', sans-serif;
    background-color: var(--primary-color);
    color: var(--text-color);
}

main {

    margin-left: auto;
    margin-right: auto;
    

}

#about,
#skills,
#contact {
    scroll-snap-align: start;
}

h1,
h2,
h3,
h4,
h5,
h6,
.animation-container {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}

#about-text-panel h2,
#skills h2,
#contact-panel h2 {
    font-size: 2rem;
    padding-bottom: 50px;
}

@media only screen and (min-width: 600px) {

    #about-text-panel h2,
    #skills h2,
    #contact-panel h2 {
        font-size: 3rem;
        padding-bottom: 40px;
    }
}

.miain-title::after {
    content: "";
    height: 2px;
    width: 70px;
    background-color: var(--hover-color);
    display: block;
    margin-top: 5px;
}

