p {
    opacity: 0.9;
    line-height: 2rem;
}

a {
    text-decoration: none;
    -webkit-transition: fill 0.3s;
    transition: fill 0.3s;
}

a:visited {
    color: unset;
    fill: unset;
}

a:hover {
    color: var(--hover-color);
    fill: var(--hover-color);
}

.shape {
    -webkit-box-shadow: inset -2px -2px 6px rgba(0, 0, 0, 0.5), inset 2px 2px 6px rgba(255, 255, 255, 0.055), 3px 3px 3px rgba(0, 0, 0, 0.5);
            box-shadow: inset -2px -2px 6px rgba(0, 0, 0, 0.5), inset 2px 2px 6px rgba(255, 255, 255, 0.055), 3px 3px 3px rgba(0, 0, 0, 0.5);
    background: linear-gradient(135deg, var(--overlay-color) 0%, #121318 100%);
}

.background {
    background: var(--bg-vin-color);
    background-attachment: fixed;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
    /* overflow-y: scroll; */
    /* test */
}

#skills,
#about,
#contact {
    margin-left: auto;
    margin-right: auto;
    padding: 50px 15px;
    max-width: 1000px;
}

@media only screen and (min-width: 600px) {

    #skills,
    #about,
    #contact {
        padding: 100px 90px;
    }
}

button {
    height: 60px;
    width: 190px;
    border: 2px solid var(--text-color);
    background-color: transparent;
    color: var(--text-color);
    font-weight: 400;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 0.174em;
    -webkit-transition: all 0.1s;
    transition: all 0.1s;
}

button:hover {
    background-color: rgba(255, 255, 255, 0.082);
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    border: 2px solid var(--hover-color);
    color: var(--hover-color);
}

.buttonhover {
    -webkit-animation: buttonhover 1s ease-in-out;
            animation: buttonhover 1s ease-in-out;
}

@-webkit-keyframes buttonhover {


    0% {
        border-bottom: 2px solid var(--hover-color);
    }

    33% {
        border-right: 2px solid var(--hover-color);
    }

    33% {
        border-top: 2px solid var(--hover-color);
    }

    100% {
        border-left: 2px solid var(--hover-color);
    }
}

@keyframes buttonhover {


    0% {
        border-bottom: 2px solid var(--hover-color);
    }

    33% {
        border-right: 2px solid var(--hover-color);
    }

    33% {
        border-top: 2px solid var(--hover-color);
    }

    100% {
        border-left: 2px solid var(--hover-color);
    }
}